*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.calendar {
  background: #fff;
  flex-wrap: wrap;
  gap: 1px;
  width: 726px;
  margin: 0 auto;
  padding: 10px;
  display: flex;
}

.calendar__weekday {
  text-align: center;
  color: #444;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 30px;
  font-weight: bold;
  display: flex;
}

.calendar__day {
  cursor: pointer;
  background: #eee;
  border: 1px solid #000;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  transition: all .5s;
  display: flex;
  position: relative;
}

.calendar__day:before {
  content: "";
  color: #333;
  font-family: Arial, sans-serif;
  font-size: 30px;
  font-weight: bold;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.calendar__day:hover {
  background: #ffbfcb;
  transform: translateY(-20px);
}

